<template>
    <div id="operate-content">
        <header class="myHeader">
            <div class="form-group">
                <div class="select-group">
                    <span class="lableName">账户：</span>
                    <select id="select-mac" class="select-header" v-model="chooseUserId" @change="selectUser">
                        <option v-for="item in userList" :value="item.user_id" :title="item.user_name" :key="item.user_id">{{item.user_name}}</option>
                    </select>
                </div>
                <div class="header-time">
                    <span>时间选择：</span>
                    <el-date-picker
                            v-model="timeValue"
                            type="datetimerange"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            align="right"
                            @change="change()"
                    >
                    </el-date-picker>
                </div>
            </div>
            <div class="search-div">
                <input type="text" class="record-input" placeholder="关键字" v-model="recordVal" @input="searchRecord">
                <i class="iconfont icon-zhongzi-sousuo search-icon" @click="searchRecord"></i>
            </div>
            <button class="headerBtn" @click="tableToExcel">导出数据</button>
        </header>
        <div class="content">
            <div class="table_block">
                <table class="index_table">
                    <thead>
                    <tr>
                        <th>序号</th>
                        <th>操作时间</th>
                        <th>操作账号</th>
                        <th>操作描述</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="item in lastData">
                        <td>{{item.index}}</td>
                        <td>{{item.occur_time}}</td>
                        <td>{{item.user_name}}</td>
                        <td>{{item.detail}}</td>
                    </tr>
                    </tbody>
                </table>
                <div class="noData" v-show="noLogData">当前用户无操作日志</div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "OperateLog",
        components: {},
        data() {
            return {
                chooseUserId:'',//所选的用户id
                userList:[],//用户列表
                lastData:[],//最后显示的数据列表
                activeNum:5,//当前界面的index
                recordVal:'',//搜索的内容
                origRecordList:[],//获取到的所有的日志列表信息
                noLogData:false,//是否有日志数据
                pickerOptions: {
                    shortcuts: [{
                        text: '最近一周',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近一个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近三个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        },
                    }, {
                        text: '最近半年',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 180);
                            picker.$emit('pick', [start, end]);
                        },
                    }, {
                        text: '最近1年',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 365);
                            picker.$emit('pick', [start, end]);
                        },
                    }
                    ]
                },
                timeValue: [new Date(new Date - 7*24*3600*1000), new Date],
            }
        },
        created: function(){
            this.$emit('ievent', this.activeNum);
        },
        destroyed:function () {
            let obj = {};
            let linkInfo = JSON.parse(sessionStorage.getItem('paramsConfig'));
            if(!$.isEmptyObject(linkInfo)){
                obj = linkInfo;
            }
            sessionStorage.setItem('paramsConfig',JSON.stringify(obj));
        },
        mounted(){
            this.getHeadData();
        },
        methods: {
            //获取组织、机组信息
            getHeadData() {
                let that = this;
                that.userList.length = 0;
                let type = Number(sessionStorage.getItem('userType'));
                if(type===0){
                    this.$axios.post('getAllUser',type)
                        .then(res => {
                            if(res && res.length>0){
                                res.forEach(user =>{
                                    that.userList.push({
                                        "user_id":user.id,
                                        "user_name":user.username
                                    })
                                });
                                that.chooseUserId = that.userList[0].user_id;
                                that.getPosData();
                            }

                        }).catch(err => {
                        console.log(err)
                    })
                }
                else{
                    that.userList=[
                        {
                            "user_id":Number(sessionStorage.getItem('user_id')),
                            "user_name":sessionStorage.getItem('username'),
                        }
                    ];
                    that.chooseUserId = Number(sessionStorage.getItem('user_id'));
                    that.getPosData();
                }
            },
            //改变用户选择
            selectUser(){
                this.getPosData();
            },
            change(){
                this.getPosData();
            },
            //获取日志信息
            getPosData(){
                let that = this;
                if(that.chooseUserId!=''){
                    let params=null;
                    params = {
                        user_id:that.chooseUserId,
                        start_time:(this.timeValue[0]).getTime(),
                        end_time:(this.timeValue[1]).getTime()
                    };
                    that.noLogData = false;
                    this.$axios.post('getClientLog',params)
                        .then(res => {
                            that.lastData = [];
                            if(res.msg===0 && res.list.length>0){
                                for(let i=0;i<res.list.length; i++){
                                    let obj = {};
                                    obj.index = Number(i+1);
                                    obj.occur_time = res.list[i].occur_time;
                                    obj.user_name = res.list[i].user_name;
                                    obj.detail = res.list[i].log_desc;
                                    that.lastData.push(obj);
                                }
                                that.origRecordList = that.lastData.concat();
                            }
                            else{
                                that.noLogData = true;
                            }
                        }).catch(err => {
                        console.log(err)
                    })
                }
            },
            //搜索日志
            searchRecord(){
                var reg = RegExp(this.recordVal);
                var that = this;
                if (that.recordVal.length>0) {
                    that.lastData = [];
                    // 过滤需要的数据
                    for(let i=0;i<that.origRecordList.length;i++){
                        if (reg.test(that.origRecordList[i].detail)) {
                            that.lastData.push(that.origRecordList[i]);
                        }
                    }
                }
                else{
                    that.lastData=that.origRecordList.concat();
                }

            },
            //转换日期方法
            translateDate(value){
                value = new Date(value);
                value = value.getFullYear() + '-'
                    + (value.getMonth() + 1) + '-'
                    + this.fixTime(value.getDate()) + ' '
                    + this.fixTime(value.getHours()) + ':'
                    + this.fixTime(value.getMinutes()) + ':'
                    + this.fixTime(value.getSeconds());
                return value;
            },
            //补0
            fixTime (str) {
                if (Number(str) < 10) {
                    str = "0" + str;
                }
                return str;
            },
            buildTime: function(date) {
                var t = new Date(date);
                return t.getFullYear() + '-' +
                    (this.fixTime(t.getMonth() + 1)) + '-' +
                    this.fixTime(t.getDate());
            },
            //切换按钮
            toggleBtn(){
                this.isReal = !this.isReal;
                if(this.isReal){
                    this.toggleText = '实时';
                }
                else{
                    this.toggleText = '历史';
                }
            },
            //导出数据
            tableToExcel(){
                let userName = '';
                if(this.userList.length){
                    this.userList.forEach(user=>{
                        if(user.user_id==this.chooseUserId){
                            userName = user.user_name;
                        }
                    })
                }
                //要导出的json数据
                var jsonData = this.lastData;
                //列标题，逗号隔开，每一个逗号就是隔开一个单元格
                let str = `序号,操作时间,操作账号,操作描述\n`;
                //增加\t为了不让表格显示科学计数法或者其他格式
                for(let i = 0 ; i < jsonData.length ; i++ ){
                    for(let item in jsonData[i]){
                        str+=`${jsonData[i][item] + '\t'},`;
                    }
                    str+='\n';
                }
                //encodeURIComponent解决中文乱码
                let uri = 'data:text/csv;charset=utf-8,\ufeff' + encodeURIComponent(str);
                //通过创建a标签实现
                var link = document.createElement("a");
                link.href = uri;
                //对下载的文件命名
                link.download =  `操作日志表_${userName}.csv`;
                document.body.appendChild(link);
                link.click();
                document.body.removeChild(link);
            },
        },
    }
</script>

<style lang="scss">
    $baseBlueColor: #436EF6;
    #operate-content{
        .myHeader{
            height: 80px;
            line-height: 80px;
            padding: 0 20px;
            background: #fff;
            .form-group{
                float: left;
                .select-group{
                    display: inline-block;
                    margin-right: 90px;
                    .lableName{
                        font-size: 20px;
                        vertical-align: middle;
                    }
                    .select-header{
                        font-size: 14px;
                        min-width:100px;
                        height:30px;
                        border:1px solid #D9D9D9;
                        border-radius:8px;
                        cursor: pointer;
                        vertical-align: middle;
                        &:focus{
                            outline: none;
                            border:1px solid #C9DBEF;
                        }
                    }

                }
                @media (max-width: 1400px) {
                    .select-group{
                        .lableName{
                            font-size: 16px;
                        }
                    }
                    .header-time{
                        span{
                            font-size: 16px;
                        }
                    }
                }
            }
            .header-time{
                display: inline-block;
                span{
                    font-size: 20px;
                    vertical-align: middle;
                }
                .am-form-field{
                    font-size: 14px;
                    padding: 0 12px;
                    max-width:100px;
                    height:30px;
                    border:1px solid #D9D9D9;
                    border-radius:8px;
                    cursor: pointer;
                    vertical-align: middle;
                    &:focus{
                        outline: none;
                        border:1px solid #C9DBEF;
                    }
                }
                .start_date{
                    margin-right: 90px;
                }
            }
             @media (max-width: 1400px) {
                 .header-time{
                    span{
                        font-size: 16px;
                    }
                 }
             }
            .search-div{
                position: absolute;
                right: 150px;
                top: 0;
                .record-input{
                    font-size: 14px;
                    min-width:100px;
                    height:30px;
                    padding: 0 12px;
                    border:1px solid #D9D9D9;
                    border-radius:8px;
                    cursor: pointer;
                    vertical-align: middle;
                    &:focus{
                        outline: none;
                        border:1px solid #C9DBEF;
                    }
                }
                .search-icon{
                    position: absolute;
                    right: 12px;
                    top: 0;
                    cursor: pointer;
                    color: #7D7D7D;
                    &:hover{
                        color: darken(#7D7D7D,20%);
                    }
                    &:active{
                        color: darken(#7D7D7D,5%);
                    }
                }
            }
            .headerBtn{
                position: absolute;
                right: 20px;
                top: 25px;
                width:100px;
                height:30px;
                background:$baseBlueColor;
                border-radius:4px;
                outline: none;
                border: none;
                color: #fff;
                &:hover{
                    background:darken($baseBlueColor,10%);
                }
            }

        }
        .content{
            background: #e4e8f4;
            padding: 20px;
            flex: 1;
            .table_block{
                width: calc(100vw - 200px);
                overflow: auto;
                position: relative;
                .noData{
                    position: absolute;
                    top: 50%;
                    left: 45%;
                    text-align: center;
                    font-size: 18px;
                }
                .index_table{
                    border-spacing: 0;
                    width: calc(100vw - 200px);
                }
                .index_table th,.index_table td{
                    height: 38px;
                    /*line-height: 38px;*/
                    text-align: center;
                    padding: 0!important;
                    width: 1%;
                    vertical-align: middle !important;
                }
                .index_table td{
                    font-size: 14px;
                }
                .index_table>thead>tr,.index_table>tbody>tr{
                    display:table;
                    width:100%;
                    table-layout:fixed;
                }
                .index_table>thead>tr{
                    border-top-right-radius: 8px;
                    border-top-left-radius: 8px;
                    &>th{
                        height: 60px;
                        line-height: 60px;
                        font-size: 16px;
                    }
                }
                .index_table>thead{
                    display :block;
                }
                .index_table>tbody{
                    display:block;
                    height: calc(100vh - 200px);
                    font-size: 14px;
                    /*height: 800px;*/
                    -ms-scroll-chaining: chained;
                    -ms-overflow-style: none;
                    -ms-content-zooming: zoom;
                    -ms-scroll-rails: none;
                    -ms-content-zoom-limit-min: 100%;
                    -ms-content-zoom-limit-max: 500%;
                    -ms-scroll-snap-type: proximity;
                    -ms-scroll-snap-points-x: snapList(100%, 200%, 300%, 400%, 500%);
                    -ms-overflow-style: none;
                    overflow: auto;
                }
                .index_table>tbody>tr:nth-child(odd){
                    background-color:#EFF1F6;
                }
                .index_table>tbody>tr:nth-child(even){
                    background-color:#fff;
                }
                .index_table>tbody>tr:hover{
                    background-color: #abb7c7;
                }
                .index_table>thead>tr{
                    background-color: #C9DBEF;
                }
            }
        }
    }
</style>
